<template>
    <div>
        <MyHeader />
        <div class="container-fluid p-5" style="margin-top: 90px">
            <div class="d-flex justify-content-between mb-3 align-items-center">
                <table class="table table-hover bg-white border rounded">
                    <thead>
                        <tr class="text-center">
                            <th></th>
                            <th>/</th>
                            <th>, </th>
                            <th>. </th>
                            <th></th>
                            <th>/</th>
                            <th></th>
                            <th></th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>
                                <input type="text" class="form-control" placeholder="" v-model="formData.company_name">
                            </td>
                            <td>
                                <input type="number" min="0" class="form-control" placeholder="/" v-model="formData.id_code">
                            </td>
                            <td>
                                <input type="text" class="form-control" placeholder=", " v-model="formData.name">
                            </td>
                            <td>
                                <input type="email" class="form-control" placeholder=". " v-model="formData.email">
                            </td>
                            <td>
                                <input type="number" min="0" class="form-control" placeholder="" v-model="formData.mobile">
                            </td>
                            <td>
                                <input type="number" min="0" class="form-control" placeholder="/" v-model="formData.personal_id">
                            </td>
                            <td>
                                <select class="form-select" v-model="formData.status">
                                    <option value="" selected disabled></option>
                                    <option value="active"></option>
                                    <option value="inactive"></option>
                                    <option value="pending">pending</option>
                                </select>
                            </td>
                            <td>
                                <select class="form-select" v-model="formData.permission">
                                    <option value="" selected disabled></option>
                                    <option value="coordinator"></option>
                                    <option value="company"></option>
                                    <option value="operator"></option>
                                </select>
                            </td>
                            <td>
                                <button type="button" class="btn btn-success w-100" :disabled="disabled" @click="searchUser">
                                    <span class="spinner-border spinner-border-sm" v-if="loader"></span>
                                    <span v-else><BIconSearch /></span>
                                </button>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>

            <div class="d-flex justify-content-between mb-3 align-items-center">
                <h4 class="text-muted"> </h4>

                <router-link to="/user/add" class="btn btn-success"> </router-link>
            </div>

            <table class="table table-hover bg-white border rounded">
                <thead>
                    <tr class="text-center">
                        <th>ID</th>
                        <th></th>
                        <th>/</th>
                        <th>, </th>
                        <th>. </th>
                        <th></th>
                        <th>/</th>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    <tr class="text-center" v-for="data in users.data" :key="data.id">
                        <td>{{ data.id }}</td>
                        <td>{{ data.company_name }}</td>
                        <td>{{ data.identification_code }}</td>
                        <td>{{ data.name }}</td>
                        <td>{{ data.email }}</td>
                        <td>{{ data.mobile }}</td>
                        <td>{{ data.personal_id }}</td>
                        <td>{{ data.status }}</td>
                        <td>{{ (data.permission == 'company') ? '' : (data.permission == 'coordinator') ? '' : '' }}</td>
                        <td>
                            <router-link :to="'/user/edit/' + data.id" type="button" class="btn btn-warning" v-tippy="{ content: '' }">
                                <BIconPencilSquare />
                            </router-link>
                        </td>
                    </tr>
                </tbody>
            </table>
            <Pagination v-model="page" :records="Number(users.total)" :per-page="Number(users.per_page)" @paginate="getUsers"/>
        </div>
    </div>
</template>

<script>
    import MyHeader from "../../components/Header.vue";
    import axios from "axios";
    import Pagination from 'v-pagination-3';

    export default {
        name : "UsersGrid",

        components : {
            MyHeader,
            Pagination
        },

        data() {
            return {
                users : [],
                
                formData : {
                    company_name : "",
                    id_code : "",
                    name : "",
                    email : "",
                    mobile : "",
                    personal_id : "",
                    status : "",
                    permission : ""
                },

                disabled : false,
                loader : false,

                page : 1
            }
        },

        methods : {
            getUsers(page = 1) {
                axios.get("/user/list?page=" + page, {
                    headers : {
                        "Authorization" : "Bearer " + JSON.parse(window.localStorage.getItem("token"))
                    }
                }).then(response => {
                    this.users = response.data;
                }).catch(err => {
                    console.log(err);
                })
            },

            searchUser() {
                this.disabled = true;
                this.loader = true;
                
                axios.post("/search/user", this.formData, {
                    headers : {
                        "Authorization" : "Bearer " + JSON.parse(window.localStorage.getItem("token"))
                    }
                }).then(response => {
                    this.users = response.data;
                    this.disabled = false;
                    this.loader = false;
                }).catch(err => {
                    console.log(err);
                    this.disabled = false;
                    this.loader = false;
                });
            }
        },

        mounted() {
            document.title = "";

            this.getUsers();
        }
    }
</script>