<template>
    <div>
        <MyHeader />

        <div class="container" style="margin-top: 120px">
            <div class="row justify-content-center">
                <div class="col-md-6">
                    <form @submit.prevent="edit">
                        <div class="form-group mb-3">
                            <label for="currentPassword"> </label>
                            <input type="text" class="form-control" id="currentPassword" v-model="title">
                        </div>
                        <div class="form-group mb-3">
                            <label for="status"></label>
                            <select class="form-select" id="status" v-model="status">
                                <option value="" selected disabled> </option>
                                <option value="enabled"></option>
                                <option value="disabled"></option>
                            </select>
                        </div>
                        <div class="d-grid">
                            <button type="submit" class="btn btn-success" :disabled="disabled">
                                
                                <span v-if="loader" class="spinner-border spinner-border-sm"></span>
                            </button>
                        </div>
                    </form>

                    <div v-for="(item, index) in errors" :key="index" class="alert alert-danger mt-3">
                        <strong>{{ item[0] }}</strong>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import axios from "axios";
    import MyHeader from "../../components/Header.vue";

    export default {
        name : "AddProduct",

        data() {
            return {
                title: '',
                status : "",

                errors : [],
                
                disabled : false,
                loader : false,
            };
        },
        
        components : {
            MyHeader,
        },

        mounted() {
            document.title = " ";

            axios.get("/product/get/" + this.$route.params.id, {
                headers : {
                    "Authorization" : "Bearer " + JSON.parse(window.localStorage.getItem("token"))
                }
            }).then(response => {
                this.title = response.data.name;
                this.status = response.data.status;
            }).catch(err => {
                console.log(err);
            });
        },

        methods: {
            edit() {
                this.disabled = true;
                this.loader = true;
                
                axios.put('/product/edit/' + this.$route.params.id, { title: this.title, status : this.status }, {
                    headers : {
                        "Authorization" : "Bearer " + JSON.parse(window.localStorage.getItem("token"))
                    }
                }).then(response => {
                    this.errors = [];

                    this.$swal({
                        title : " ",
                        icon : "success",
                    });
                    
                    this.disabled = false;
                    this.loader = false;
                }).catch(error => {
                    this.errors = error?.response?.data?.errors;
                    this.disabled = false;
                    this.loader = false;
                });
            }
        }
    };
</script>

