<template>
    <div>
        <MyHeader />

        <div class="container-fluid" style="margin-top: 84.5px">
            <div class="row vh-100">
                <div class="col-6">
                    <div class="container">
                        <h1></h1>
                        <p><strong> , :</strong> {{ data.beneficiary_name }}</p>
                        <p><strong> :</strong> {{ data.overhead_number }}</p>
                        <p><strong> :</strong> {{ data.overhead_date }}</p>
                        <p><strong> :</strong> {{ data.store_address }}</p>
                        <p><strong>  4 :</strong> {{ data.card_number }}</p>
                        <p><strong> :</strong> {{ data.full_amount }}</p>

                        <hr>

                        <table class="table table-bordered bg-white border rounded-5">
                            <thead>
                                <tr>
                                    <th></th>
                                    <th></th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="(item, index) in data.statement_products" :key="index">
                                    <td>{{ item.products?.name }} </td>
                                    <td>{{ item.price }} </td>
                                </tr>
                            </tbody>
                        </table>
                        <div class="col-12">
                            <button type="button" @click="changeStatus" class="btn btn-success"> </button>
                        </div>
                    </div>
                </div>
                <div class="col-6">
                    <iframe width="100%" height="100%" :src="'http://localhost:8000/api/statement/pdf/' + this.$route.params.id"></iframe>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import MyHeader from "../../components/Header.vue";
    import axios from "axios";

    export default {
        name : "StatementRead",

        components : {
            MyHeader,
        },

        data() {
            return {
                user_id : JSON.parse(window.localStorage.getItem("user")).user_id,
                permission : JSON.parse(window.localStorage.getItem("user")).permission,

                data : []
            }
        },

        mounted() {
            document.title = "";

            axios.get("/statement/get/" + this.$route.params.id, {
                headers : {
                    "Authorization" : "Bearer " + JSON.parse(window.localStorage.getItem("token"))
                }
            }).then(response => {
                this.data = response.data;
            }).catch(err => {
                console.log(err);
            })
        },

        methods : {
            changeStatus() {
                axios.put("/statement/change/status/" + this.$route.params.id, {
                    headers : {
                        "Authorization" : "Bearer " + JSON.parse(window.localStorage.getItem("token"))
                    }
                }).then(response => {
                    console.log(response.data);
                }).catch(err => {
                    console.log(err);
                });
            }
        }
    }
</script>
